import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      name: "index",
      path: "/",
      components: {
        foot: () => import('../components/Footer.vue'),
        main: () => import('../components/index.vue')
      }
    },
    {
      name: "mall",
      path: "/mall",
      components: {
        foot: () => import('../components/Footer.vue'),
        main: () => import('../components/Mall.vue')
      }
    },
    {
      name: "my",
      path: "/my",
      components: {
        foot: () => import('../components/Footer.vue'),
        main: () => import('../components/my.vue')
      }
    },
    {
      name: "product",
      path: "/product",
      components: {
        main: () => import('../components/cart/Product.vue')
      }
    },
    {
      name: "cart",
      path: "/cart",
      components: {
        main: () => import('../components/cart/cart.vue')
      }
    },
    {
      name: "submitOrder",
      path: "/submitOrder",
      components: {
        main: () => import('../components/cart/submitOrder.vue')
      }
    },
    {
      name: "myOrder",
      path: "/myOrder",
      components: {
        main: () => import('../components/user/myOrder.vue')
      }
    },
    {
      name: "deviceMange",
      path: "/deviceMange",
      components: {
        main: () => import('../components/user/deviceMange.vue')
      }
    },
    {
      name: "activeDevice",
      path: "/activeDevice",
      components: {
        main: () => import('../components/user/activeDevice.vue')
      }
    },
    {
      name: "teamOrder",
      path: "/teamOrder",
      components: {
        main: () => import('../components/user/teamOrder.vue')
      }
    },
    {
      name: "address",
      path: "/address",
      components: {
        main: () => import('../components/cart/address.vue')
      }
    },
    {
      name: "setAddress",
      path: "/setAddress",
      components: {
        main: () => import('../components/cart/setAddress.vue')
      }
    },
    {
      name: "dataCenter",
      path: "/dataCenter",
      components: {
        main: () => import('../components/user/dataCenter.vue')
      }
    },
    {
      name: "myTeam",
      path: "/myTeam",
      components: {
        main: () => import('../components/user/myTeam.vue')
      }
    },
    {
      name: "myCoupon",
      path: "/myCoupon",
      components: {
        main: () => import('../components/user/myCoupon.vue')
      }
    },
    {
      name: "setting",
      path: "/setting",
      components: {
        main: () => import('../components/user/setting.vue')
      }
    },
    {
      name: "user_info",
      path: "/user_info",
      components: {
        main: () => import('../components/user/user_info.vue')
      }
    },
    {
      name: "myPoints",
      path: "/myPoints",
      components: {
        main: () => import('../components/user/myPoints.vue')
      }
    },
    {
      name: "myCard",
      path: "/myCard",
      components: {
        main: () => import('../components/user/myCard.vue')
      }
    },
    {
      name: "saleCard",
      path: "/saleCard",
      components: {
        main: () => import('../components/user/saleCard.vue')
      }
    },
    {
      name: "invite",
      path: "/invite",
      components: {
        main: () => import('../components/user/invite.vue')
      }
    },
    {
      name: "orderDetail",
      path: "/orderDetail",
      components: {
        main: () => import('../components/user/orderDetail.vue')
      }
    },
    {
      name: "myCharge",
      path: "/myCharge",
      components: {
        main: () => import('../components/user/myCharge.vue')
      }
    },
    {
      name: "applyWithdraw",
      path: "/applyWithdraw",
      components: {
        main: () => import('../components/user/applyWithdraw.vue')
      }
    },
    {
      name: "signPoints",
      path: "/signPoints",
      components: {
        main: () => import('../components/user/signPoints.vue')
      }
    },
    {
      name: "login",
      path: "/login",
      components: {
        main: () => import('../components/login.vue')
      }
    },
    {
      name: "setPhone",
      path: "/setPhone",
      components: {
        main: () => import('../components/user/setPhone.vue')
      }
    },
    {
      name: "machine",
      path: "/machine",
      components: {
        main: () => import('../components/user/machine.vue')
      }
    }
  ],
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    } else {
      return { x: 0, y: 0 }
    }
  }
})



