let url = {
  index: '/Index/index',
  productDetail: '/Goods/goodsInfo',
  addCart: '/Cart/add',
  cartList: '/Cart/index',
  deleteCart: '/Cart/delete',
  changeCart: '/Cart/changeNum',
  bugNow: '/cart/settlement',
  address: '/user/address_list',
  addAddress: '/user/addressSave',
  delAddress: '/user/del_address',
  addOrder: '/cart/addOrder',
  addressDetail: '/user/getRegionByParentId',
  userInfo: '/User/index',
  orderList: '/order/order_list',
  orderDetail: '/order/order_detail',
  myPoints: '/user/points_list',
  myCard: '/user/currency_list',
  saleCard: '/user/currency_business',
  myCharge: '/user/account_list',
  saleCardnow: '/user/currency_sell',
  userSign: '/user/user_sign',
  signTotal: '/user/sign',
  changeUserInfo: '/User/userinfo',
  share: '/user/share_qrcode_user',
  bindPhone: '/user/real_mobile',
  bindName: '/user/real_name'
}

//开发环境和真实环境的切换
//let host = ''
let host = ' http://admin.he01.cn/index.php/api'

for (let key in url) {
  if (url.hasOwnProperty(key)) {
    url[key] = host + url[key]
  }
}

export default url


